@import url(https://fonts.googleapis.com/css?family=Lato:300,400,400i,700,900&display=swap);
@import url(https://fonts.googleapis.com/css?family=Lato:300,400,400i,700,900&display=swap);
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
.navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #000000;
  padding: 10px 16px; }
  @media all and (max-width: 400px), all and (max-width: 767px) {
    .navigation {
      padding: 8px 12px; } }

.product-select {
  display: flex;
  justify-content: space-evenly;
  align-items: center; }
  .product-select .button {
    padding: 0; }

@media all and (min-width: 768px) and (max-width: 991px), all and (min-width: 992px) and (max-width: 1199px), all and (min-width: 1200px) {
  .mobile-product-list {
    display: none; } }

@media all and (max-width: 400px), all and (max-width: 767px) {
  .mobile-product-list {
    display: flex;
    justify-content: space-between;
    align-items: center; } }

@media all and (min-width: 768px) and (max-width: 991px), all and (min-width: 992px) and (max-width: 1199px), all and (min-width: 1200px) {
  .desktop-product-list {
    display: flex;
    justify-content: space-between;
    align-items: center; } }

@media all and (max-width: 400px), all and (max-width: 767px) {
  .desktop-product-list {
    display: none; } }

.desktop-product-list .MuiListItem-root {
  width: unset; }
  @media all and (max-width: 400px), all and (max-width: 767px) {
    .desktop-product-list .MuiListItem-root {
      width: 100%; } }

.desktop-product-list .MuiList-padding {
  padding: 0 !important; }

.desktop-product-list .MuiListItem-gutters {
  padding: 0; }

.desktop-product-list .MuiPaper-root {
  color: #abaab2 !important; }

.desktop-product-list .MuiListItemText-root {
  margin: 0 !important; }

.desktop-product-list .MuiTypography-body1 {
  font-family: 'Lato', sans-serif !important;
  color: #abaab2;
  line-height: 1 !important;
  letter-spacing: 0.3px !important;
  font-weight: 700 !important; }

.desktop-product-list .MuiMenuItem-root {
  font-family: 'Lato', sans-serif !important;
  font-size: 16px !important;
  line-height: 1 !important;
  letter-spacing: 0.3px !important;
  font-weight: 700 !important;
  color: #abaab2; }

.desktop-product-list .MuiListItem-root.Mui-selected, .desktop-product-list .MuiListItem-root.Mui-selected:hover {
  background-color: rgba(0, 0, 0, 0.05);
  color: #ffffff; }

.mobile-menu-chevron {
  margin-left: 8px; }
  @media all and (max-width: 400px) {
    .mobile-menu-chevron {
      flex-basis: 10%;
      margin-left: 4px; } }

.product-link {
  font-family: 'Lato', sans-serif;
  font-size: 16px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.38;
  letter-spacing: 0.3px;
  color: #abaab2;
  -webkit-transition: all 250ms ease 10ms;
  transition: all 250ms ease 10ms;
  line-height: 1;
  font-weight: 700;
  list-style: none;
  margin: 0 0 2px 24px;
  cursor: pointer; }
  .product-link:hover {
    color: #ffffff; }
  .product-link:active {
    color: #ffffff; }
  @media all and (max-width: 400px) {
    .product-link {
      text-align: center; } }

.active {
  color: #ffffff; }

.MuiMenu-paper {
  border-radius: 0px !important; }

.MuiList-padding {
  padding: 0 !important; }

.MuiListItem-gutters {
  padding: 0; }

.MuiPaper-root {
  color: #555555 !important;
  background-color: #f2f4f5 !important; }

.MuiListItemText-root {
  margin: 0 !important; }
  @media all and (max-width: 400px) {
    .MuiListItemText-root {
      flex-basis: 50%; } }

.MuiTypography-body1 {
  font-family: 'Lato', sans-serif !important;
  color: #ffffff;
  line-height: 1 !important;
  letter-spacing: 0.3px !important;
  font-weight: 700 !important; }

.MuiMenuItem-root {
  font-family: 'Lato', sans-serif !important;
  font-size: 16px !important;
  line-height: 1 !important;
  letter-spacing: 0.3px !important;
  font-weight: 700 !important;
  color: #555555; }

.MuiListItem-root.Mui-selected, .MuiListItem-root.Mui-selected:hover {
  background-color: rgba(0, 0, 0, 0.06) !important; }

.MuiListItem-button:hover {
  background-color: rgba(0, 0, 0, 0.06) !important;
  color: #000000 !important; }

.MuiPaper-elevation8 {
  box-shadow: unset !important; }

.navigation-menu {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  justify-self: flex-end; }

.hamburger {
  cursor: pointer; }
  .hamburger .line {
    height: 1.4px;
    background-color: #ffffff;
    border-radius: 30px;
    margin: 4px 0;
    width: 24px; }

.login-button {
  font-family: 'Lato', sans-serif;
  font-size: 14px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.43;
  letter-spacing: 0.3px;
  color: #ffffff;
  font-weight: 700;
  line-height: 1;
  letter-spacing: 1px;
  text-transform: uppercase;
  background-color: #000000;
  border: 1px solid #ffffff;
  border-radius: 30px;
  padding: 8px 24px;
  cursor: pointer;
  outline: none;
  -webkit-transition: all 300ms ease 10ms;
  transition: all 300ms ease 10ms;
  margin-right: 24px; }
  .login-button:hover {
    font-family: 'Lato', sans-serif;
    font-size: 14px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.43;
    letter-spacing: 0.3px;
    color: #638601;
    font-weight: 700;
    line-height: 1;
    letter-spacing: 1px;
